                                        style="position: absolute; width: inherit; height: 1px; background-color: rgb(255, 255, 255); transition: 0.3s; transform: rotate(135deg);">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div
                            style="width: inherit; height: 2px; opacity: 0.25; background-image: linear-gradient(black, transparent);">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            rotatShowStatus: false,
            timer: null,
            isLoading: true,
            configurationHeight: "640",
            paytableContentHeight: "577",
            ops: {
                vuescroll: {},
                scrollPanel: {
                    scrollingX: false, //关闭横向滚动
                },
                rail: {
                    keepShow: true,
                },
                bar: {
                    hoverStyle: true,
                    onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
                    background: "hsla(0, 0%, 46%)", //滚动条颜色
                    opacity: 0.7, //滚动条透明度
                },
            },
        }
    },
    mounted() {
        this.configurationHeight = window.configurationHeight
        this.paytableContentHeight = window.paytableContentHeight
    },
    methods: {
        open() {
            this.isLoading = true
            this.rotatShowStatus = true
            this.timer = setTimeout(() => {
                this.isLoading = false
            }, 1000)
            this.$nextTick(() => {
                this.$refs.scrollContainer.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
            });
        },

        closeTable() {
            this.rotatShowStatus = false
            window.changeTrophiesIconShow(true);
        }
    }
}
</script>

