<template>
	<div id="game-history-container"
		:style="`pointer-events:none;overflow: hidden; visibility: visible; width: 360px; transform: scale(1); position: relative; z-index: 98;height:${configurationHeight}px;`">
		<div id="container-view" style="overflow: hidden;">
			<div style="top: 0%; left: 0px; position: absolute; width: inherit; height: inherit;">
				<div id="game-list-view" class="game-list-view-container">
					<div id="game-list-view-wrapper" style="flex-direction: column;">
						<!-- <clique></clique> -->
					</div>
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	import {
		data
	} from "browserslist"
	import historyDetails from "./history-details.vue"
	import clique from "./clique.vue";
	export default {
		components: {
			historyDetails,
			clique
		},
		data() {
			return {
				timeTypeName: 'Hoje',
				timeType: 0,
				minTime: new Date('2024-1-1'),
				maxTime: new Date(),
				timeList: [2024, 1, 1, 2024, 9, 15],
				timeListStatr: [false, false, false, false, false, false],
				timeListDay: [
					[],
					[],
					[],
					[],
					[],
					[]
				],
				timeShow: true,
				recordNum: 0,
				stakeNum: '0,00',
				profitNum: '0,00',
				configurationHeight: '640',
				showDetails: false,
				hideDetails: false,
				showTime: 0,
				timezone: "(GMT+8:00)",
				titleList: ["Histórico do jogo", "Selecionar intervalo de datas", "Personalizado"],
				data: {
					token: null,
					game_id: null,
					start_ts: null,
					end_ts: null,
					page_size: 10,
					page: 1
				},
				isLoading: false,
				isPage: true,
				orderNo: '',
				dataList: [],
				ops: {
					vuescroll: {},
					scrollPanel: {
						scrollingX: false, //关闭横向滚动
					},
					rail: {
						keepShow: true,
					},
					bar: {
						hoverStyle: true,
						onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
						background: "hsla(0, 0%, 46%)", //滚动条颜色
						opacity: 0.7, //滚动条透明度
					},
				},
				isShrink:false,
				isShow:false
			}
		},
		mounted() {
			this.configurationHeight = window.configurationHeight
			this.data.token = window.token
			this.data.game_id = window.gameId
			this.updataTime(0)
			this.infoList()
		},
		methods: {
			onScroll(e) {
				let scroll = this.$refs["vs"].getScrollProcess()
				if (scroll.v === 1) {
					// 触发加载
					this.data.page++
					this.getList()
				}
			},
			loadingList() {
				if (this.data.page >= 2) {
					this.isLoading = !this.isLoading
				} else {
					this.$parent.changeLoading()
				}
			},
			confirmTime() {
				this.timeList[1] = this.timeList[1] < 10 ? '0' + this.timeList[1] : this.timeList[1];
				this.timeList[2] = this.timeList[2] < 10 ? '0' + this.timeList[2] : this.timeList[2];
				this.timeList[4] = this.timeList[4] < 10 ? '0' + this.timeList[4] : this.timeList[4];
				this.timeList[5] = this.timeList[5] < 10 ? '0' + this.timeList[5] : this.timeList[5];

				let start_ts = this.timeList[0] + "-" + this.timeList[1] + "-" + this.timeList[2] + " 00:00:00"
				let end_ts = this.timeList[3] + "-" + this.timeList[4] + "-" + this.timeList[5] + " 23:59:59"

				this.data.start_ts = Date.parse(new Date(start_ts)) / 1000
				this.data.end_ts = Date.parse(new Date(end_ts)) / 1000
				this.timeTypeName = this.timeList[0] + "/" + this.timeList[1] + "/" + this.timeList[2] + " - " + this
					.timeList[3] + "/" + this.timeList[4] + "/" + this.timeList[5]

				this.infoList()
				this.showTime = 0
			},
			infoTimeList() {
				let time = new Date()
				var year = time.getFullYear();
				var month = time.getMonth() + 1;
				var strDate = time.getDate();
				this.timeList = [year, month, strDate, year, month, strDate]

				this.getTimeYear()
				this.getTimeMonths(year)
				this.getTimeDay(year, month)
			},
			selectTimeDay(type, itme) {
				this.timeList[type] = itme
				let date = new Date()
				if (type < 3) {
					date = new Date(this.timeList[0], this.timeList[1] - 1, this.timeList[2])
				} else {
					date = new Date(this.timeList[3], this.timeList[4] - 1, this.timeList[5])
				}
				if (type === 0 || type === 3) {
					var year = date.getFullYear();
					if (year === this.maxTime.getFullYear()) {
						var month = this.maxTime.getMonth() + 1;
						var strDate = this.maxTime.getDate();
						this.timeList = [year, month, strDate, year, month, strDate]
					} else {
						var month = 12;
						this.timeList = [year, month, 31, year, month, 31]
					}
					this.getTimeMonths(year)
					this.getTimeDay(year, month)
				}
				if (type === 1 || type === 4) {
					var year = date.getFullYear();
					var month = date.getMonth() + 1;
					if (year === this.maxTime.getFullYear() && month === (this.maxTime.getMonth() + 1)) {
						var strDate = this.maxTime.getDate();
						this.timeList = [year, month, strDate, year, month, strDate]
					} else {
						let dateDay = new Date(year, month - 1, 31)
						let startDate, endDate;
						if (type === 1) {
							startDate = dateDay
							endDate = new Date(dateDay.setHours(0, 0, 0, 0) + 24 * 60 * 60 * 1000 * 7)
							if (endDate > this.maxTime) {
								endDate = this.maxTime
							}
						} else {
							startDate = new Date(dateDay.setHours(0, 0, 0, 0) - 24 * 60 * 60 * 1000 * 7)
							endDate = dateDay
							if (startDate < this.minTime) {
								startDate = this.minTime
							}
						}
						var startYear = startDate.getFullYear();
						var startMonth = startDate.getMonth() + 1;
						var startDay = startDate.getDate();
						var endYear = endDate.getFullYear();
						var endMonth = endDate.getMonth() + 1;
						var endDay = endDate.getDate();
						this.timeList = [startYear, startMonth, startDay, endYear, endMonth, endDay]
					}
					this.getTimeDay(year, month)
				}
				if (type === 2 || type === 5) {
					let startDate, endDate;
					if (type === 2) {
						startDate = date
						endDate = new Date(date.setHours(0, 0, 0, 0) + 24 * 60 * 60 * 1000 * 7)
						if (endDate > this.maxTime) {
							endDate = this.maxTime
						}
					} else {
						startDate = new Date(date.setHours(0, 0, 0, 0) - 24 * 60 * 60 * 1000 * 7)
						endDate = date
						if (startDate < this.minTime) {
							startDate = this.minTime
						}
					}
					var startYear = startDate.getFullYear();
					var startMonth = startDate.getMonth() + 1;
					var startDay = startDate.getDate();
					var endYear = endDate.getFullYear();
					var endMonth = endDate.getMonth() + 1;
					var endDay = endDate.getDate();
					this.timeList = [startYear, startMonth, startDay, endYear, endMonth, endDay]
				}
				this.showSelect(type)
			},
			showSelect(type) {
				// 显示隐藏日期
				this.timeShow = false
				if (this.timeListStatr[type]) {
					this.timeListStatr[type] = false
				} else {
					this.timeListStatr = [false, false, false, false, false, false]
					this.timeListStatr[type] = true
				}
				this.$nextTick(() => {
					this.timeShow = true
				})
			},
			getTimeYear() {
				let year = this.maxTime.getFullYear()
				let yearNum = new Date(this.maxTime - this.minTime).getFullYear() - 1970
				let yearType = []
				let i = 0
				do {
					yearType.push(year - i);
					i++;
				} while (yearNum >= i);
				this.timeListDay[0] = yearType.reverse()
				this.timeListDay[3] = yearType.reverse()
			},
			getTimeMonths(year) {
				const now = this.maxTime
				let monthType = []
				for (let i = 0; i < 12; i++) {
					let epoch = new Date(year, i, 1)
					if (now > epoch) {
						monthType.push(i + 1)
					}
				}
				this.timeListDay[1] = monthType
				this.timeListDay[4] = monthType
			},
			getTimeDay(year, month) {
				const now = this.maxTime
				let dayType = []
				let epoch = new Date(year, month - 1)
				epoch.setMonth(epoch.getMonth() + 1, 0);
				for (let i = 1; i <= epoch.getDate(); i++) {
					let a = new Date(year, month - 1, i)
					if (now > a) {
						dayType.push(i)
					}
				}
				this.timeListDay[2] = dayType
				this.timeListDay[5] = dayType
			},
			selectTimeType(type, name) {
				this.timeType = type
				this.timeTypeName = name ? name : this.timeTypeName
				if (type < 2) {
					this.updataTime(type)
					this.infoList()
					this.showTime = 0
				} else {
					this.infoTimeList()
					this.showTime = 2
				}
			},
			updataTime(type) {
				let time = new Date()
				let start_ts = null
				let end_ts = null
				if (type == 0) {
					start_ts = Date.parse(new Date(new Date().setHours(0, 0, 0, 0))) / 1000; //获取当天零点的时间
					end_ts = Date.parse(new Date(new Date().setHours(0, 0, 0, 0) + 24 * 60 * 60 * 1000 - 1)) /
						1000; //获取当天23:59:59的时间
				} else if (type == 1) {
					start_ts = Date.parse(new Date(new Date().setHours(0, 0, 0, 0) - 24 * 60 * 60 * 1000 * 7)) /
						1000; //获取当天零点的时间
					end_ts = Date.parse(new Date(new Date().setHours(0, 0, 0, 0) - 1)) / 1000; //获取当天23:59:59的时间
				}
				this.data.start_ts = start_ts
				this.data.end_ts = end_ts
			},
			changeDetails(i) {
				let _this = this
				// 延时触发v-if
				if (this.hideDetails) {
					setTimeout(function() {
						_this.hideDetails = false
					}, 750)
				} else {
					this.hideDetails = !this.hideDetails
					if (i != null) {
						this.orderNo = this.dataList[i].order_no
					}
				}
				this.showDetails = !this.showDetails

			},
			closeHistory() {
				this.$parent.changeHistory()
			},
			infoList() {
				this.data.page = 1
				this.dataList = []
				this.isPage = true
				this.getList()
			},
			getList() {
				this.loadingList()
				this.$http.post('/api/order/get_list',
					JSON.stringify(this.data)).then(response => {
					this.loadingList()
					if (response.data.meta.code == 0) {
						this.recordNum = response.data.data.stat.total_count
						this.stakeNum = response.data.data.stat.total_bet_gold ? this.formatArray(response.data
							.data.stat.total_bet_gold) : this.formatArray('000')
						this.profitNum = response.data.data.stat.total_profit ? this.formatArray(response.data.data
							.stat.total_profit) : this.formatArray('000')

						let rows = response.data.data.rows
						if (rows.length) {
							rows.map(item => {
								item.localeTime = this.getLocaleTime(item.bet_time)
								item.orderNo = this.getOrderNo(item.order_no)
								item.betGold = this.formatArray(item.bet_gold)
								item.profitGold = this.formatArray(item.profit)
							})
							this.dataList = this.dataList.concat(rows)
						} else {
							this.isPage = false
						}
					}
				}).catch(error => {
					this.loadingList()
				});
			},
			getLocaleTime(time) {
				let timeList = ['-', '-', '-']
				if (time) {
					let date = new Date(time * 1000)
					let yy = date.getFullYear();
					let mm = date.getMonth() + 1;
					mm = mm < 10 ? '0' + mm : mm;
					let dd = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
					let hh = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
					let mf = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
					let ss = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
					timeList[0] = mm + "/" + dd
					timeList[1] = hh + ":" + mf + ":" + ss
					timeList[2] = yy + "/" + mm + "/" + dd + " " + hh + ":" + mf + " " + this.timezone
				}
				return timeList
			},
			getOrderNo(order_no) {
				let orderNo = '-'
				if (order_no) {
					var firstPart = order_no.slice(0, 9); // 获取字符串的前n-1个字符
					var secondPart = order_no.slice(9); // 获取字符串的第n个字符及之后的字符
					orderNo = firstPart + " " + secondPart
				}
				return orderNo
			},
			formatArray(num) {
				let numStr = (num / Math.pow(10, 2)).toFixed(2);
				let result = [];
				let reverseList = [];

				for (let i = numStr.length - 1; i < numStr.length && i >= 0; i--) {
					reverseList.push(numStr[i]);
				}

				for (let i = 0; i < reverseList.length; i++) {
					if (reverseList[i] == '.') {
						result.push('.');
					} else if (((i - (2 + 1)) % 3) === 0 && i > (2 + 1)) {
						if (reverseList[i] !== '-') {
							result.push(',');
						}
						result.push(reverseList[i]);
					} else {
						result.push(reverseList[i]);
					}
				}
				return result.reverse().join('')
			},
		}
	}
</script>

<style>
	.hideList {
		animation: leftAnimate .75s ease;
	}

	.showAnimate {
		animation: leftAnimate .75s ease;
		animation-fill-mode: forwards;
	}

	.hideAnimate {
		animation: rightAnimate .75s ease;
		animation-fill-mode: forwards;
	}

	.upPopupAnimate {
		animation: upAnimate .75s ease;
		animation-fill-mode: forwards;
	}

	.downPopupAnimate {
		animation: downAnimate .75s ease;
		animation-fill-mode: forwards;
	}

	@keyframes hideListPage {
		from {
			display: none;
		}

		to {
			display: block;
		}
	}

	@keyframes leftAnimate {
		from {
			transform: translateX(100%);
			display: none;
		}

		to {
			transform: translateX(0%);
			display: block;
		}
	}

	@keyframes rightAnimate {
		from {
			transform: translateX(0%);
			display: block;
		}

		to {
			transform: translateX(100%);
			display: none;
		}
	}

	@keyframes upAnimate {
		from {
			transform: translateY(0%);
			display: block;
		}

		to {
			transform: translateY(-100%);
			display: none;
		}
	}

	@keyframes downAnimate {
		from {
			transform: translateY(-100%);
			display: none;
		}

		to {
			transform: translateY(0%);
			display: block;
		}
	}

