    z-index: 998;
    background-color: #041a2e;
    animation: shake 0.82s ease;
    animation-fill-mode: forwards;
}


@keyframes shake {
    from {
        transform: translateY(100%);
        display: none;
    }

    to {
        transform: translateY(0%);
        display: block;
    }
}

.rules1 {
    position: absolute;
