<script>
export default {
    data() {
        return {
            show: false,
            type: 2,
            isLoading: true,
            timer: null,
            countown: "0:30",
            time: 30,
            timerTorneio: null,
            torneioTabType: 1,
            currentTime: "",
            isClose: true
        }
    },
    methods: {
        open() {
            this.isClose = false
            this.type = 0
            this.isLoading = true
            this.show = true
            this.updateTime()
            this.timer = setTimeout(() => {
                this.isLoading = false
                this.isClose = true
            }, 2000)
            window.resetGameView();
        },

        close() {
            if(this.isClose == true){
                clearInterval(this.timerTorneio)
                this.show = false
                this.$emit('TrophiesIconShow')
            }
        },

        openMenu() {
            this.type = 1
        },

        closeMenu() {
            this.type = 0
        },

        openTorneio() {
            this.isClose = false
            setTimeout(()=>{
		        window.resetGameView();
	        }, 0.25)
            clearInterval(this.timerTorneio);
            this.type = 2
            this.time = 30
            this.countown = "0:30"
            this.isLoading = true
            this.timer = setTimeout(() => {
                this.isLoading = false
                this.timerTorneio = setInterval(() => {
                    if (this.time > 0) {
                        this.time -= 1
                        this.countown = `${0}:${this.time < 10 ? '0' : ''}${this.time}`
                    } else {
                        clearInterval(this.timerTorneio)
                        this.openTorneio()
                    }
                    window.resetGameView();
                }, 1000)
                window.resetGameView();
                this.isClose = true
            }, 2000)
            window.resetGameView();
        },

        changeTorneioTab(e) {
            if (this.isLoading == false) {
                this.torneioTabType = e
                clearInterval(this.timerTorneio)
                this.openTorneio()
            }
        },

        getCurrentTime() {
            const now = new Date();
            return `${String(now.getHours()).padStart(2, '0')}:${String(now.getMinutes()).padStart(2, '0')}:${String(now.getSeconds()).padStart(2, '0')}`
        },
        updateTime() {
            this.currentTime = this.getCurrentTime()
        }
    }
}
</script>

